import axios from "axios";

const service = axios.create({
  baseURL: process.env.VUE_APP_URL,
  timeout: 10000,
});
const showStatus = (status: number) => {
  let message = "";
  // Ëøô‰∏ÄÂù®‰ª£Á†ÅÂèØ‰ª•‰ΩøÁî®Á≠ñÁï•Ê®°ÂºèËøõË°å‰ºòÂåñ
  switch (status) {
    case 400:
      message = "ËØ∑Ê±ÇÈîôËØØ(400)";
      break;
    case 401:
      message = "Êú™ÊéàÊùÉÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï(401)";
      break;
    case 403:
      message = "ÊãíÁªùËÆøÈóÆ(403)";
      break;
    case 404:
      message = "ËØ∑Ê±ÇÂá∫Èîô(404)";
      break;
    case 408:
      message = "ËØ∑Ê±ÇË∂ÖÊó∂(408)";
      break;
    case 500:
      message = "ÊúçÂä°Âô®ÈîôËØØ(500)";
      break;
    case 501:
      message = "ÊúçÂä°Êú™ÂÆûÁé∞(501)";
      break;
    case 502:
      message = "ÁΩëÁªúÈîôËØØ(502)";
      break;
    case 503:
      message = "ÊúçÂä°‰∏çÂèØÁî®(503)";
      break;
    case 504:
      message = "ÁΩëÁªúË∂ÖÊó∂(504)";
      break;
    case 505:
      message = "HTTPÁâàÊú¨‰∏çÂèóÊîØÊåÅ(505)";
      break;
    default:
      message = `ËøûÊé•Âá∫Èîô(${status})!`;
  }
  return `${message}ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÊàñËÅîÁ≥ªÁÆ°ÁêÜÂëòÔºÅ`;
};
// ÂìçÂ∫îÊã¶Êà™Âô®
service.interceptors.response.use(
  (response) => {
    return response.data;
  },
  (error) => {
    // ÈîôËØØÊäõÂà∞‰∏öÂä°‰ª£Á†Å
    const status = error.response.status;
    let msg = showStatus(status);

    error.data = {};
    error.data.msg = "ËØ∑Ê±ÇË∂ÖÊó∂ÊàñÊúçÂä°Âô®ÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÊàñËÅîÁ≥ªÁÆ°ÁêÜÂëòÔºÅ";
    if (typeof error.response.data.Value != undefined) {
      error.msg = error.response.data.Value;
      return Promise.reject(error);
    }
    var returnMessage = error.response.data.Message; //error.response.data.data
    if (
      typeof returnMessage == undefined ||
      returnMessage == null ||
      returnMessage == ""
    ) {
      returnMessage = error.response.data.Form;
      if (
        typeof returnMessage == undefined ||
        returnMessage == null ||
        returnMessage == ""
      ) {
        error.msg = msg;
      } else {
        error.msg = Object.values(returnMessage)[0];
      }
    } else {
      error.msg = returnMessage;
    }
    return Promise.reject(error);
  }
);
// ËØ∑Ê±ÇÊã¶Êà™Âô®
service.interceptors.request.use(
  (config) => {
    return config;
  },
  (err) => {
    return Promise.reject(err); //ËøîÂõûÈîôËØØ
  }
);

export default service;
