<template>
  <div class="container">
    <div class="post-center">
      <div class="nickname">‰ª£Á†ÅÁîüÊàêÂô®</div>
      <div class="description">
        <p>CaomeiËá™Áî®Áâà <br />Ê∑±Â∫¶Âº∫Ëø´ÁóáÊÇ£ËÄÖ</p>
      </div>
      <el-form :inline="false" :model="formInline" labelPosition="top">
        <el-row :gutter="20">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6"> </el-col>
          <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">
            <el-form-item label="UI">
              <el-input v-model="formInline.UI" :disabled="true" />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6"> </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6"> </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-form-item label="*ÈÄâÊã©Ê®°Âûã">
              <el-select
                @change="onSelectedDrug"
                v-model="formInline.SelectedModel"
                class="m-2"
                placeholder="*ËØ∑ÈÄâÊã©Ê®°Âûã"
                size="large"
              >
                <el-option
                  v-for="item in formInline.AllModels"
                  :key="item.Value"
                  :label="item.Text"
                  :value="item.Value"
                />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6">
            <el-form-item label="Âå∫Âüü">
              <el-input v-model="formInline.Area" />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6"> </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6"> </el-col>
          <el-col :xs="24" :sm="6" :md="6" :lg="6" :xl="6">
            <el-form-item label="*ËÆ§ËØÅÊñπÂºè">
              <el-select
                v-model="formInline.AuthMode"
                class="m-2"
                placeholder="ËØ∑ÈÄâÊã©ËÆ§ËØÅÊñπÂºè"
                size="large"
              >
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6">
            <el-form-item label="*Ê®°ÂùóÂêçÁß∞">
              <el-input v-model="formInline.ModuleName" />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6"> </el-col>
        </el-row>

        <el-form-item>
          <div class="post-center">
            <el-button type="primary" @click="onSubmit">‰∏ã‰∏ÄÊ≠•</el-button>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import router from "@/router";
import { SetField } from "../api";
import { AllModels } from "../api";
import { isEmpty } from "element-plus/lib/utils";

export default {
  data() {
    return {
      formInline: {},
      options: [
        {
          value: "Both",
          label: "Both Jwt and Cookie",
        },
        {
          value: "Jwt",
          label: "Jwt",
        },
        {
          value: "Cookie",
          label: "Cookie",
        },
      ],
      UI: isEmpty(this.$route.query.ui) ? "BlazorAndVue" : this.$route.query.ui,
    };
  },
  components: {},
  methods: {
    AllModels() {
      AllModels()
        .then((res) => {
          this.formInline.AllModels = res;
          this.formInline.UI = this.UI;
          this.formInline.ModuleName = "MenuKey.";
        })
        .catch((err) => {
          this.$message.error(err.msg);
          console.log(err);
          console.log(err.msg);
        });
    },
    onSelectedDrug() {
      var objVal = {};
      this.formInline.AllModels.forEach((item) => {
        // this.systemVersionObj ÊòØ‰∏ãÊãâÊ°ÜÁöÑÊï∞ÊçÆ
        if (item.Value == this.formInline.SelectedModel) {
          // ÈÄâ‰∏≠idÂíåÂΩìÂâç‰∏ÄÊ†∑Â∞±ËµãÂÄº
          objVal = item.Text;
        }
      });
      this.formInline.ModelName = objVal; // ËµãÂÄº
    },
    SetField() {
      SetField({
        UI: this.formInline.UI,
        SelectedModel: this.formInline.SelectedModel,
        ModuleName: this.formInline.ModuleName,
        Area: this.formInline.Area,
      })
        .then((res) => {
          this.$router.push({
            name: "SetField",
            params: {
              formInline: JSON.stringify(this.formInline),
              entity: JSON.stringify(res.Data),
            },
          });
        })
        .catch((err) => {
          this.$message.error(err.msg);
          console.log(err);
          console.log(err.msg);
        });
    },

    onSubmit() {
      if (
        this.isEmpty(this.formInline.AuthMode) ||
        this.isEmpty(this.formInline.SelectedModel) ||
        this.formInline.ModuleName == "MenuKey." ||
        this.isEmpty(this.formInline.ModuleName)
      ) {
        this.$message.error("ÂøÖÂ°´È°πÊú™Â°´");
      } else {
        this.SetField();
      }
    },
    //Âà§Êñ≠Â≠óÁ¨¶ÊòØÂê¶‰∏∫Á©∫ÁöÑÊñπÊ≥ï
    isEmpty(obj) {
      if (typeof obj == "undefined" || obj == null || obj == "") {
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {},
  created() {
    this.AllModels();
  },
};
</script>
